import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Table } from 'react-bootstrap';
import Player from './player.js';
import Stat from './stat.js';


function MyTeam(props) {

    const [선수, 선수변경] = useState(Player);
    const [stat, stat변경] = useState(Stat);
  
    return (
      <div>
          <Table responsive="sm" className="statTable">
            <thead>
              <tr>
                <th></th>
                <th>등급</th>
                <th>득점</th>
                <th>유효 슈팅</th>
                <th>도움</th>
                <th>키 패스</th>
                <th>태클</th>
                <th>가로채기</th>
              </tr>
            </thead>
            {
              선수.map((a, i)=>{
                return <tbody>
                <tr>
                  <td>{ 선수[i].name }</td>
                  <td>S</td>
                  {/* <td>{ rank() }</td> */}
                  <td>{ 선수[i].goal }</td>
                  <td>{ 선수[i].assist }</td>
                  <td>{ 선수[i].shot }</td>
                  <td>{ 선수[i].pass }</td>
                  <td>{ 선수[i].tackle }</td>
                  <td>{ 선수[i].intercept }</td>
                </tr>
              </tbody>
              })
            }
          </Table>
      </div>
    )
  }

export default MyTeam